<script>
import axios from 'axios';
import TheHeader from "./../../components/TheHeader";
import { selIndexList,uploadSource } from "./../../api/index";
export default {
  data() {
    return {
      dataList: [],
      roleCode: 0,
      taskCount: "",
      pageSize: 8, // 一页显示多少条数据
      currentPage: 1, // 当前页
      fileList1: [],
      fileList2: [],
      dataForm:{
        id:'',
        date:'',
        remuneration:'',
        remark:'',
        source:'',
        priority:false
      },
      tableData: [
        {
          id: '',
          name: '',
          content: ''
        }
      ]
    };
  },
  components: {
    TheHeader,
  },

  created() {
    // this.getDataList();
  },
  computed: {
    // 计算当前表格中的数据
    data () {
      return this.tableData.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
    }
  },
  mounted() {},
  methods: {
    getDataList() {
      selIndexList()
        .then((res) => {
          // console.log(res);
          if (res.code == 200) {
            this.dataList = res.data;
            this.roleCode = res.role_code;
            this.taskCount = res.task_count;
          } else {
            this.notify(res.msg ? res.msg : "登录失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);       
        });
    },
    identify(){
        uploadSource({
          source:this.dataForm.source
        })
        .then((res) => {
          // console.log(res);
          if (res.code == 200) {
            this.tableData=res.data
          } else {
            this.notify(res.msg ? res.msg : "获取失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);       
        });
    },
	BooleanJude(val){
	  let  bool = 'False';
	  if(val=='true'||val==true){
	    bool='True'
	  }else if(val=='false'||val==false){
	    bool='False'
	  }
	  return bool
	},
    submit(){
      let forms = new FormData();
      forms.append("custom_id",this.dataForm.id);
      forms.append("Finished_time",this.dataForm.date);
      forms.append("sourcefile",this.fileList1[0]);
      forms.append("formatfile",this.fileList2[0]);
      forms.append("money",this.dataForm.remuneration);
      forms.append("remarks",this.dataForm.remark);
      forms.append("source",this.dataForm.source);
      forms.append("priority",this.dataForm.priority);
      axios({
          method:"post",
          url:"http://119.91.38.178:8080/api/vueuploadtask/",
          data:forms
        }).then((res)=>{
          if (res.data.code == 200) {
            console.log('2222');
            this.$confirm('您已成功提交表单，是否前往首页？', '恭喜您', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'success',
              center: true
            }).then(() => {
              this.$router.push({path:`/`})
            }).catch(() => {
              
            });
          } else {
            this.$message({
              message: res.data.msg?res.data.msg:'设置失败',
              type: 'error'
            });
          }
        })
      // uploadtask({
      //     custom_id:this.dataForm.id,
      //     Finished_time:this.dataForm.date,
      //     sourcefile:sourcefile,
      //     formatfile:this.fileList2[0],
      //     money:this.dataForm.remuneration,
      //     remark:this.dataForm.remark,
      //     source:this.dataForm.source,
      //     priority:this.dataForm.priority
      // })
      //   .then((res) => {
      //     console.log(res);
      //     if (res.code == 200) {
      //       this.tableData=res.data
      //     } else {
      //       this.notify(res.msg ? res.msg : "获取失败", "error");
      //     }
      //   })
      //   .catch((err) => {
      //     console.log(err);       
      //   });
    },
    handleCurrentChange (val) {
      this.currentPage = val
    },
    getBirthDate (date) {
    this.dataForm.date = date
    },
    // 页面切换
    handleSizeChange(){
        console.log("页面切换1");
    },
    // 页面切换
    currentChange(){
        console.log("页面切换2");
    },
    handleRemove1(file, fileList) {
        console.log("hhhhh1:",file, fileList);
    },
    handleRemove2(file, fileList) {
        console.log("hhhhh2:",file, fileList);
    },
    handlePreview1(file) {
        console.log("1hhhhh:",file);
    },
    handlePreview2(file) {
        console.log("2hhhhh:",file);
    },
    handleExceed1(files, fileList1) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList1.length} 个文件`);
    },
    handleExceed2(files, fileList1) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList1.length} 个文件`);
    },
    beforeRemove1(file, fileList1) {
      this.fileList1=[]
    },
    beforeRemove2(file, fileList2) {
        // return this.$confirm(`确定移除 ${ file.name }？`);
        this.fileList2=[]
    },
    upload1Click(file, fileList1){
      this.fileList1=[]
      this.fileList1.push(file.file)
		
    },
    upload2Click(file, fileList1){
      this.fileList2=[]
      this.fileList2.push(file.file)

    }
  },
};
</script>
