<template>
  <div class="header-box">
    <div class="main-center header-main">
      <div class="header-logo">知迅Studio</div>
      <div class="header-nav-box">
        <div class="header-nav" v-for="item in menuList" :key="item.id" 
        :class="item.id==ins?' header-nav header-nav-on':'header-nav'" 
        @click="toPush(item.id==2?'':item.path)">
          {{ item.id==2&&titleName?titleName:item.name }}
        </div>
      </div>
      <div class="header-exit-box">
        <el-button type="primary" @click="toExit()">退出登录</el-button>
      </div>
    </div>
  </div>
</template>
<script>


export default {
  data () {
    return {
      menuList:[
        {id:1,name:'首页',path:'/'},
        {id:2,name:'填写',path:'/'},
        {id:5,name:'我的',path:'/my'}
      ],
      userList:[
        {id:1,name:'首页',path:'/'},
        {id:2,name:'填写',path:'/'},
        {id:5,name:'我的',path:'/my'}
      ],
      superList:[
        {id:1,name:'首页',path:'/'},
        {id:2,name:'填写',path:'/'},
        {id:3,name:'上传任务',path:'/uptask'},
        {id:4,name:'格式设置',path:'/'},
        // {id:4,name:'格式设置',path:'/format'},
        {id:5,name:'我的',path:'/my'}
      ],
      userName:''
    }
  },
  computed: {
    
  },
  created(){
    const userRole = JSON.parse(
        window.localStorage.getItem("userInfo")
      ).user_role;
    this.userName=JSON.parse(
        window.localStorage.getItem("userInfo")
      ).username
      
    if(userRole=='fill'){
      this.menuList=this.userList
    }else if(userRole=='all'||userRole=='超级管理'||userRole=='for'||userRole=='填写'){
      this.menuList=this.superList
    }  
  },
  mounted () {
    
  },
  props:{
    ins:{
      type:Number,
      default:1
    },
    titleName:{
      type:String,
      default:'填写'
    }
  },
  methods: {
    toPush(path){
      if(path){
        this.$router.push({path})
      }
      
    },
    toExit(){
      window.localStorage.clear();
      this.$router.push('/login')
      
    }
  }
}
</script>
<style scoped>
.header-box {
  width: 100%;
  height: 70px;
  font-size: 22px;
  background: #fff;
  border-bottom: 1px solid #666;
  
}
.header-main{
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
}
.header-logo{
  font-size: 28px;
  color: #4774B6;
}
.header-nav-box{
  margin-left: 10%;
  display: flex;
}
.header-nav{
  margin: 0 20px;
  font-size: 18px;
  line-height: 36px;
  cursor: pointer;
}
.header-nav-on{
  background: #4774B6;
  color: #fff;
  padding: 0 20px;
  line-height: 36px;
}
.header-exit-box{
  position: absolute;
  right: 0;
}
</style>
